import { Form, Input, Modal } from "antd-mobile";

type ConfirmType = {
  open: boolean;
  setOpen: (s: boolean) => void;
  onSubmit: (o) => void;
};

export function Confirm({ open, setOpen, onSubmit }: ConfirmType) {
  const [form] = Form.useForm();

  function onConfirm() {
    form
      .validateFields()
      .then((val) => {
        onSubmit(val);
        setOpen(false);
      })
      .catch((e) => {
        console.log(e);
      });
  }

  return (
    <Modal
      visible={open}
      content={
        <Form form={form}>
          <Form.Header>
            
          </Form.Header>
          <Form.Item
            name="mobile"
            label=""
            rules={[
              { required: true },
              { pattern: /^1[3-9]\d{9}$/, message: "" }
            ]}
          >
            <Input placeholder="" />
          </Form.Item>
          <Form.Item
            name="wechat"
            label=""
            rules={[
              { required: true },
              {
                pattern: /^[a-zA-Z]([-_a-zA-Z0-9]{5,19})+$/,
                message: ""
              }
            ]}
          >
            <Input placeholder="" />
          </Form.Item>
        </Form>
      }
      // closeOnMaskClick: true,
      //
      actions={[
        {
          key: "confirm",
          text: "",
          onClick: onConfirm
        }
      ]}
    />
  );
}
